.trailsText {
    position: relative;
    width: 100%;
    height: 80px;
    line-height: 80px;
    color: #D79922 ;
    font-size: 2em;
    font-weight: 800;
    letter-spacing: -0.05em;
    will-change: transform, opacity;
    overflow: hidden;
    
    
  }
  
  .trailsText > div {
    padding-right: 0.05em;
    overflow: hidden;
    
    
  }

  
  .container {
    display: flex;
    align-items: center;
    height: 100%;
    justify-content: center;
    flex-direction: row;
    width: 60%;
    background-color: #EFE2BA ;
    border-radius: 20px;


  }
